import { FirebaseAnalytics } from '@capacitor-firebase/analytics';
import BaseProvider from './base-provider';

class FirebaseAnalyticsProvider extends BaseProvider {
    async initialize() {
        // keep this empty
    }

    async trackPageView() {
        await Promise.all([
            FirebaseAnalytics.logEvent({
                name: 'pageview',
                params: {
                    page_location: window.location.href,
                    page_title: document.title,
                },
            }),
            FirebaseAnalytics.setCurrentScreen({
                screenName: window.location.href,
            }),
        ]);
    }

    async trackEvent(eventName, eventParams) {
        await FirebaseAnalytics.logEvent({
            name: eventName,
            params: eventParams,
        });
    }

    async setUserId(userId) {
        await FirebaseAnalytics.setUserId({ userId });
    }

    async setEmail(email) {
        await FirebaseAnalytics.setUserProperty({ key: 'email', value: email });
    }

    async setUserProperties(properties) {
        await Promise.all(
            Object.entries(properties).map(([key, value]) =>
                FirebaseAnalytics.setUserProperty({ key, value })
            )
        );
    }
}

export default FirebaseAnalyticsProvider;
